.cards_field {
  border-radius: 5px;
  border: 3px solid #FFD565;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0 10px;

  min-height: 275px;
  padding: 5px;
max-width: 885px;
  width: 100%;
}

.english_words, .russian_words {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  & .card_word {
    background: rgba(255, 255, 255, 0.2);
    border: 2px solid #9F4A4A;
    margin: 2px;
    border-radius: 5px;
    padding: 15px 0px;
    min-width: 160px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    cursor: pointer;
  }
  & div:hover{
    border: 2px solid #FFD565;
  }
  & div.active{
    border: 2px solid #FFD565;
  }
  & div.disable{
    border: 2px solid #FFD565;
    background-color: #9F4A4A;
    opacity: 0.4;
    cursor: auto;
  }
  & div.false{
    //border: 2px solid #f44336;
    //background-color: #f4433629;
    border: 2px solid #FFD565;
    background-color: #ffd56566;
  }
}

.russian_words {
  margin-top: 20px;

}
@media all and (max-width: 577px){
  .english_words, .russian_words {
    & div {
      min-width: 132px;
    }
  }
}