@import url('https://fonts.googleapis.com/css2?family=Luckiest+Guy&family=Noto+Sans:ital,wght@0,400;0,700;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');


body{
  font-family: 'Noto Sans', sans-serif;
  color: #000000;
  font-size: 18px;
}

a{
  &:hover{
    text-decoration: none!important;
    color: #000000;
  }
}
.container{
  margin: 0 auto;
  padding: 0 100px!important;
}
a{
  text-decoration: none;
  color: #000000;
}
h1, h2, h3{
  font-family: 'Russo One', sans-serif;
}
h1{
  text-align: center;
  font-size: 48px;
  margin: 90px 0 30px;
}
.flex{
  display: flex;
  justify-content: center;
}

.flex-column{
  display: flex;
  flex-direction: column;
}
.align-center{
  align-items: center;
}
.flex-between{
  display: flex;
  justify-content: space-between;
}

.flex-start{
  display: flex;
  justify-content: flex-start;
}
.flex-wrap{
  display: flex;
  flex-wrap: wrap;
}
.btn{
  transition: 0.5s all ease;
  cursor: pointer;
  &:hover{
    opacity: 0.5;
  }
}
main{
  min-height: calc(100vh - 132px);
  height: 100%;
}
ul{
  padding-left: 0;
  li{
    list-style: none;
  }
}

@media (max-width: 992px){
  h1, .h1{
    font-size: 2rem!important;
  }
  .container {
    padding: 0 20px!important;
  }
  .info-block{
    iframe{
      width: 100%;
    }
  }
}