.main-screen {
  background-image: url(https://staticcdn-new.lingualeo.com/neo-static/bg-all.a3994c8c91d1d5d5d10424ea1c919f70.svg);
  background-position-y: 100%;
  height: 100vh;
  max-height: 800px;
  width: 100%;
  background-repeat: no-repeat;
}

.main-screen__main {
  display: flex;
  font-family: sans-serif;
  width: 500px;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .main-screen__title {
    color: #6cffde;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 10px;
    font-size: 3em;
    font-weight: lighter;
  }
  .main-screen__description {
    color: #ffffff;
    text-align: center;
    font-size: 1.1em;
    line-height: 1.5em;
  }
  .main-screen__button {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
    outline: none;
    background-color: #00c49d;
    border-radius: 10px;
    border: none;
    width: 250px;
    height: 50px;
    color: #ffffff;
    font-size: 1.5em;
    transition: .2s;
    &:hover {
      background-color: #00a586;
    }
  }
}
