.parrot_field {
  max-width: 484px;
  width: 100%;
  min-height: 476px;
  height: 100%;
  border: 2px solid #ffffff;
  border-radius: 10px;
  background-color: #ffffff0f;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  position: relative;

  &  svg{
    color: #ffffff;
    width: 33px;
    height: 33px;
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;

  }

  &  svg:hover{
    color: #2582e7;
  }
}
.parrot_field_true{
  border: 2px solid #28c38a;
}
.parrot_field_false{
  border: 2px solid #ed593b;
}
.btn_translate{
  transition: all 0.4s ease-in;
  &:before {
    content: '';
    background-color: aliceblue;
    border-radius: 50%;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: scale(0.001, 0.001);
  }
  &:focus {
    outline: 0;
    color: #fff;
    &:before {
      animation: effect_dylan 0.8s ease-out;
    }
  }
}
@keyframes effect_dylan {
  50% {
    transform: scale(1.5, 1.5);
    opacity: 0;
  }
  99% {
    transform: scale(0.001, 0.001);
    opacity: 0;
  }
  100% {
    transform: scale(0.001, 0.001);
    opacity: 1;
  }
}


.points {
  display: flex;
}

.point {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-color: #0d3848;
  margin: 9px;
}

.parrots_container {
  display: flex;
  flex-direction: column;
  position: relative;

  & .branch {
    position: relative;
    bottom: 18px;
  }
}

.parrots {
  display: flex;

}

.english_word {
  font-size: 36px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: normal;
  text-align: center;
  color: #fff;
}

.russian_word {
  font-size: 26px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: hsla(0, 0%, 100%, .6);
  margin-top: 12px;
}

.button_container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  & button {
    border: none;
    width: 183px;
    height: 50px;
    border-radius: 4px;
    font-size: 20px;
    font-weight: 400;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
    margin: 9px;
    cursor: pointer;
    outline: none;

  }

  & button:nth-child(odd) {
    background-color: #ed593b;
  }

  & button:nth-child(even) {
    background-color: #28c38a;
  }

  & button:nth-child(odd):hover {
    background-color: #d34d32;

  }

  & button:nth-child(even):hover {
    background-color: #23aa78;
  }
}
.bonus_score{
  display: inline-flex;
  font-size: 14px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #fff;
  margin-top: -25px;
}
.bonus_container{
  height: 25px;
}