.game_container {
  position: relative;
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  margin: 0 auto;
}

.countdown_box {
  position: absolute;
  left: 0;
  top: 0;
}

.parrot_container {
  margin: 0 auto;
  position: relative;
  max-width: 484px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.sound_icon {
  width: 23px;
  height: 24px;
background-position: 0;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
  position: absolute;
  top: 30px;
  right: 0;
}

.parrot_field {
  max-width: 484px;
  width: 100%;
  min-height: 476px;
  height: 100%;
  border: 2px solid #ffffff;
  border-radius: 10px;
  background-color: #ffffff0f;
  margin: 0 auto;
}

.game_count {
  color: #ffffff;
  font-size: 30px;
  font-weight: 600;
  padding: 0 10px 35px;
  margin: 0 auto;
}