.fullScreen_btn{
  font-size: 30px;
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 10px;
  z-index: 1;
  color: #007bff;
  & svg:hover{
    opacity: .5;
  }
}
@media (max-width: 992px) {
  .fullScreen_btn{
    right: 20px;
    top: 10px;
  }
}
.fullscreen{
  margin: 0 auto;
  width: 100%;
  background-color: white;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}