.settings{
  &__item{
    margin-top: 20px;
    cursor: pointer;
    label{
      margin-left: 20px;
      font-weight: 500;
      font-size: 1.5rem;
    }
    input{
      width: 30px;
      height: 30px;
    }
  }
}
.check{
  position: relative;
  width: 30px;
  height: 30px;
  border: 2px #D67801 solid;
  border-radius: 5px;
  input{
    position: absolute;
    width: 30px;
    height: 30px;
    left: 0;
    top: 0;
    opacity: 0;
    z-index: 2;
  }
  span{
    position: absolute;
    display: none;
    svg{

      width: 25px;
      min-width: 25px;
      height: 25px;
      path{
        fill: #D67801;
      }
    }
  }
  input:checked + span{
    display: block;
  }
}