.result {
  box-shadow: 0 0 10px;
  border: 1px solid #8f8f8f;
  border-radius: 10px;
  background-color: #ffffff;
  width: 400px;
  padding: 10px;
  margin: 0 auto;
  transition: 5s;
  .result__words-list {
    height: 200px;
    overflow: scroll;
    overflow-x: hidden;
  }
  .result__correct {
    color: #008000;
    font-weight: bold;
  }
  .result__uncorrect {
    color: #ff0000;
    font-weight: bold;
  }
  .result__word {
    font-weight: bold;
  }
}

h2 {
  text-align: center;
}

p:first-letter {
  text-transform: uppercase;
}

::-webkit-scrollbar {
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #808080;
  border-radius: 10px;
}
