.statistic_container {
  background-color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 10px;
  max-width: 784px;
  width: 100%;
  min-height: 651px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 15px;
}

.game_score {
  font-size: 34px;
  font-weight: 300;
  line-height: .85;
  color: #37383c;
  text-align: center;
}

.right_score {
  font-size: 12px;
  font-weight: 700;
  line-height: 1.33;
  color: #28c38a;
  text-transform: uppercase;
}

.mistakes_score {
  font-size: 12px;
  font-weight: 700;
  line-height: 1.33;
  color: #ed593b;
  text-transform: uppercase;
  //border-top: 1px solid #37383c;
  //width: 100%;
}

.link_start_game {
  cursor: pointer;
  border-radius: 4px;
  background-color: rgba(37, 130, 231, .08);
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
  color: #2582e7;
  padding: 10px 42px;
  margin-top: 24px;
}

.link_start_game:hover {
  background-color: rgba(37, 130, 231, .12);
}

.link_finishing_game {
  margin-top: 20px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.71;
  text-align: center;
  color: #2582e7;
  text-decoration: underline;
  cursor: pointer;
}

.link_finishing_game:hover {
  text-decoration: none;
}

.answer_container {
  display: flex;
  align-items: center;

  justify-content: center;
}

.answer_btn {
  color: dimgrey;
  background-size: 20px 18px;
  background-position: 50%;
  background-repeat: no-repeat;
  width: 32px;
  height: 32px;
  cursor: pointer;

  & svg {

    width: 33px;
    height: 33px;

    right: 15px;
    top: 15px;
    cursor: pointer;

  }

  & svg:hover {
    color: #2582e7;
  }
}

.english_answer {
  font-size: 16px;
  font-weight: 600;
  line-height: 1.25;
  color: #2582e7;
  margin-right: 6px;
  cursor: pointer;
  word-break: normal;
  max-width: calc(50% - 54px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.russian_answer {
  font-size: 16px;
  line-height: 1.25;
  color: #37383c;
  flex-grow: 1;
  word-break: normal;
  max-width: calc(50% - 54px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

}

.score_answer {
  overflow-y: scroll;
  overflow-y: scroll;
  max-width: 300px;
  width: 100%;
  max-height: 180px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}