header{
  padding: 15px 0;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background: #f5f5f5;
}

.header-logo{
  color: #D67801;
  font-family: 'Luckiest Guy', cursive;
  font-size: 32px;
  &:hover{
    color: #D67801;
  }
}
.header__sign-in{
  color: #1a181d;
  border: 1px #1a181d solid;
  padding: 10px 15px;
  border-radius: 15px;
  font-weight: bold;
}
.header__sign-up{
  color: #FFF;
  background: #1a181d;
  border: 1px #1a181d solid;
  padding: 10px 15px;
  border-radius: 15px;
  margin-left: 15px;
  font-weight: bold;
}
.header__sign-up, .header__sign-in{
  position: relative;
  transition: 0.4s all ease;
  &:hover{
    bottom: 2px;
  }
}
.popup{
  position: fixed;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 20px;
  padding: 40px 20px;
  text-align: center;
  width: calc(100% - 40px);
  max-width: 400px;
  box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.05), 0px 0.893452px 13.9222px rgba(0, 0, 0, 0.0243994), 0px 0.266004px 10.0595px rgba(0, 0, 0, 0.0155521);
  &__title{
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 24px;
  }
  input{
    border-radius: 10px;
    border: 1px #000000 solid;
    background: #fff;
    margin-bottom: 20px;
    padding: 10px 20px;
  }
  .btn{
    padding: 10px 20px;
    width: 100%;
    border-radius: 10px;
    background: #D67801;
    color: #FFFFFF;
    border: none;
    margin: 0 auto;
    text-transform: uppercase;
    font-weight: 500;
  }
  .btn:focus, input:focus{
    outline: none;
  }
  label{
    font-size: 12px;
    margin-bottom: 10px;
  }
}

.popup-container {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
  transition: .5s all ease;
}

.file-wrapper{
  position: relative;
  input {
    width: 100%;
    margin-bottom: 20px;
    opacity: 0;
  }
  span {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      cursor: pointer;
      border-radius: 7px;
      text-align: center;
      width: 100%;
      background: #ffd900;
      padding: 7px;
      font-weight: bold;
    }
}

.file-name {
  color: #ffd900;
  font-weight: bold;
  margin-bottom: 15px;
}
.btn-logout{
  span{
    margin-right: 10px;
  }
}
@media (max-width: 992px) {
  .header-logo{
    font-size: 24px;
  }
  .header__sign-in, .header__sign-up{
    padding: 5px 10px;
  }
}