.home-page {



}
.head-block{
    background-color: #f4fbff;
    background-repeat: no-repeat;
    background-attachment: scroll,fixed;
    padding: 90px 0;
    background-position: 0 100%,100% 0;
    background-size: 50% auto,50% auto;
    text-align: center;
    p{
      margin-bottom: 40px;
    }
}
.btn{
  &.btn-head{

    background: rgb(40,195,138);
    color: #FFFFFF;
    border-radius: 10px;
    padding: 10px 25px;
    text-transform: uppercase;
  }
}

.info-block{
  padding: 90px 0;
  text-align: center;
}
.advantages{
  span{
    margin-left: 10px;
    font-weight: 500;
  }
}