* {
  box-sizing: border-box;
  //padding: 0;
  //margin: 0;

}
.answers{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  &__item{
    margin: 10px;
    padding: 10px 20px;
    border-radius: 5px;
    border: 1px #ffffff solid;
    cursor: pointer;
    transition: 0.5s all ease;
    color: #ffffff;
    &:hover{
      opacity: 0.65;
    }
  }
}
.audio-call-game{
  margin: 0 auto;
  text-align: center;
  position: relative;
  max-height: 800px;
  height: 100vh;
  background-color: #0d3848;
  background-image: url("https://staticcdn-new.lingualeo.com/neo-static/bg-bottom.cff2e102d7fa3a58324a66feb2e75f59.svg"), url("data:image/svg+xml;base64,"), url("https://staticcdn-new.lingualeo.com/neo-static/bg-right.87e9ddb1cefaaec5ebb66d14a2e71169.svg");
  background-repeat: no-repeat, no-repeat, no-repeat;
  background-position: bottom, 0 0, 100% 0;
  background-size: 100%, auto, auto;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 60px;
  svg{
    margin-bottom: 60px;
    path{
      fill: #ffffff;
    }
  }
}
.sound-icon{
  width: 50px!important;
  height: 50px;
  cursor: pointer;
}
.game-field{
  padding: 150px 30px;
}
.answer-block{
  img{
    width: 200px;
    height: 200px;
    object-fit: cover;
    margin-bottom: 30px;
    border-radius: 50%;
  }
  span{
    font-weight: bold;
    font-size: 24px;
    color: #ffffff;
  }
}
.btn-next{
  margin: 40px;
  font-weight: bold;
  display: inline-block;
  border-radius: 16px;
  padding: 10px 20px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transition: 0.5s all ease;
  &:hover{
    opacity: 0.65;
    color: #ffffff;
  }
}
.audio-start{
  margin-top: 150px;
  color: #fff;
  span{
    font-size: 24px;
  }
  a{
    margin: 40px;
    font-weight: bold;
    display: inline-block;
    border-radius: 16px;
    padding: 10px 20px;
    background: #29578b;
    color: #ffffff;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    transition: 0.5s all ease;
    &:hover{
      opacity: 0.65;
    }
  }
}
.game-statistic{
  color: #ffffff;
}
@media (max-width: 992px) {
  .answers__item{
    width: 100%;
  }
}