.game_container{
  width: 100%;
}
.container_game_field{
  padding: 18px 16px 52px;
  position: relative;
  width: 100vw;
  height: 100vh;
}
.memory_container{
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;

}
.game_container{
  & .sound_icon{
    top: 6px;
    right: 100px;
    padding: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

& .box_heart {
  display: flex;
  position: absolute;
  left: 115px;
  top: 15px;
  & img{
    height: 16px;
    width: 16px;
    margin-right: 4px;
  }
}
}
.level_container{
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 82px;
  flex-wrap: wrap;
  & div{
    margin-right: 10px;
  }
  & svg{
    width: 45px;
    margin-right: 10px;
    cursor: pointer;
    padding: 3px;
  }
  & svg:hover{
    background: antiquewhite;
    border-radius: 50%;
  }
  & svg.active_level{
    background: antiquewhite;
    border-radius: 50%;
  }
  & img{
    margin-right: 10px;
    cursor: pointer;
    padding: 3px;
  }
  & img:hover{
    background: antiquewhite;
    border-radius: 50%;
  }
  & img.active_level{
    background: antiquewhite;
    border-radius: 50%;
  }
}
.statistic_box{
  height: 95vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}