.close-button {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 40px;
  height: 40px;
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  transition: 1s;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    background-color: #ccd4dc;
    width: 30px;
    height: 3px;
    transform: rotate(45deg);
  }
  &:before {
    content: '';
    position: absolute;
    left: 0;
    background-color: #ccd4dc;
    width: 30px;
    height: 3px;
    transform: rotate(-45deg);
  }
}
