* {
  box-sizing: border-box;
  //padding: 0;
  //margin: 0;

}

.mainSprint {
  height: 100%;
  min-height: 100vh;
  background-color: #0d3848;
  background-image: url("https://staticcdn-new.lingualeo.com/neo-static/bg-bottom.cff2e102d7fa3a58324a66feb2e75f59.svg"), url("data:image/svg+xml;base64,"), url("https://staticcdn-new.lingualeo.com/neo-static/bg-right.87e9ddb1cefaaec5ebb66d14a2e71169.svg");
  background-repeat: no-repeat, no-repeat, no-repeat;
  background-position: bottom, 0 0, 100% 0;
  background-size: 100%, auto, auto;
  position: relative;
  box-sizing: border-box;
  width: 100%;

}
.close_btn{
  position: absolute;
  right: 25px;
  top: 25px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  z-index: 1;
  & svg{
    color: rgb(126, 145, 159);
    width: 100%;
    height: 100%;
    z-index: 2;
  }
  & svg:hover{
    color: #ffffff;
    z-index: 2;
  }
}

.container-game {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 18px 16px 52px;
  height: 100vh;
  & .level_container{
    margin-top: 103px;
    margin-bottom: 5px;
  }
}

.game_box {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.title_count {
  margin-top: 20px;
  font-weight: normal;
  color: #ffffff;
}