
.team_container{
  margin: 40px 0 90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  & h2{
    text-align: center;
    text-transform: uppercase;
  }
  & .team_card{
    margin: 50px 0;
    max-width: 900px;
    width: 100%;
    border-radius: 5px;
    padding: 15px 10px;
    display: flex;
    //flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);

  }
  & .container_about{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 350px;
    margin-left: 10px;
    & h3{
      color: #535252;
    }

    & h5{
      color: #bfbdbd;
    }
    & .title{
      display: flex;
      align-items: center;
      justify-content: center;
      & a{
        font-size: 30px;
        margin-left: 15px;
        color: black;
        margin-bottom: 13px;
      }
      & a:hover{
       color: #D67801;
      }
      & a::after{
        content: attr(data_title);
      }
      & a:hover::after{
        content: attr(data_title);
      }
    }
  }
}
