.count_box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #ffffff;
}

.count_num {
    border: 2px solid #ffffff;
    width: 80px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    font-size: 30px;
}

