.title_statistics{
  text-align: center;
margin-top: 30px;
}
table{
  margin-top: 30px;
  & th{
    color: #D67801;
  }
  & th, td{
    padding: 4px;
    text-align: center;
  }
  & td:first-child{
    color: #29578b;
    text-align: left;
  }
  & .all_answers{
    font-weight: 600;
  }
}
.statistics_btn{
  background: none;
  outline: none;
  background: #13b881;
  border: 2px solid #13b881;
  border-radius: 5px;
  padding: 5px 10px;
  color: #fff;

  &:hover{
    opacity: .5;

  }
}