.word-card{
  margin: 30px 0;
  padding: 20px;
  border-radius: 10px;
  border: 1px black solid;
  text-align: center;
  input{
    padding: 10px 20px;
    border: 1px gray solid;
    border-radius: 5px;
    margin-top: 10px;
  }
  i{
    color: #D67801;
    background-color: #D67801;
    display: inline-block;
    padding: 5px;
    border-radius: 3px;
  }


}

.btn{
  display: inline-block;
  padding: 10px 25px;
  border-radius: 5px;
  font-weight: bold;
  margin: 0 10px;
}
.btn-check{
  background-color: #D67801;
  color: white;
}
.btn-answer{
  color: #D67801;
  background-color: white;
  border: 1px #D67801 solid;
}
.btn-next{
  background: rgb(219, 124, 245);
  color: white;
}
.btn-delete{
  background: red;
  color: #ffffff;
}
.btn-difficult{
  background: rgb(115, 72, 191);
  color: white;
}
.learned-words{
  text-align: center;
  margin: 30px;
  font-weight: bold;
  color: #23aa78;
}
.success{
  color: #23aa78;
  margin: 10px 0;
  font-weight: bold;
}
.error{
  color: red;
  font-weight: bold;
  margin: 10px 0;
}
.education-wrapper{
  margin-top: 40px;
}
.education-item{
  color: #063740;
  background: #FFFFFF;
  border: 1px solid #FAF7F7;
  box-sizing: border-box;
  box-shadow: 0px -2px 3px rgba(0, 0, 0, 0.06), 0px 18px 16px rgba(0, 0, 0, 0.05);
  border-radius: 30px;
  padding: 45px 25px 44px;
  width: 220px;
  text-align: center;
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;
  transition: 0.5s all ease;
  &:hover{
    bottom: 10px;
  }
  span{
    font-size: 24px;
    font-weight: bold;
  }
  .svg-inline--fa{
    margin: 0 auto 20px;
    width: 70px;
    height: 70px;
  }
  svg path{
    fill: #063740;
  }
  &:hover{
    color: #063740;
  }
}

.education-page{
  padding: 10px;
  margin-top: 10px;
  border-radius: 50px;
  color: #D67801;
  border: 1px #D67801 solid;
  margin-right: 5px;
  width: 45px;
  text-align: center;
  font-weight: bold;
  transition: 0.5s all ease;
  &:hover{
    opacity: 0.5;
    color: #D67801;
  }
}
.education-finish{
  font-size: 20px;
}