.sidebar{
  padding: 60px 43px;
  max-width: 280px;
  background: rgb(41,87,139);
  position: fixed;
  min-height: calc(100vh - 132px);
  height: 100%;
  z-index: 3;
  left: 0;
  top: 0;

  .svg-inline--fa{

    width: 30px;
    height: 30px;
    top: 30px;
    position: absolute;
    right: 30px;
  }
  *{
    color: #FFFFFF;
  }

  &.active{
    ul{
      display: block;
    }
  }
  ul{
    display: none;

    li{
      text-align: left;
      margin-bottom: 15px;
      transition: 0.5s all ease;
      font-size: 20px;
      font-weight: bold;
      position: relative;
      left: 0;
      .svg-inline--fa.svg-inline--fa{
        width: 20px;
        height: 20px;
        margin-right: 10px;
        position: relative;
        top: inherit;
        right: inherit;
      }
      &:hover{
        left: 10px;
      }
      a{
        &:hover{
          color: #ffffff;
        }
      }
    }
  }
}
@media (max-width: 992px){

  .sidebar {
    padding: 32px 43px 32px;
    max-width: inherit;
    width: 100%;
    min-height: inherit;
    height: inherit;
    background: #29578b;
    position: fixed;
    left: 0;
    top: inherit;
    bottom: 0;
    .svg-inline--fa.fa-w-14, .svg-inline--fa {
      top: 20px;
      right: 20px;
    }
  }
}

@media (max-width: 992px) {
  .sidebar ul li{
    margin-bottom: 0;
  }
}