.mainMemory{
  background-image: url(https://lingualeo.com/neo-static/bg-gaps.f7f20ded5a314892e8e3dcabcd3ad2a4.svg);
  background-size: cover;
  background-position: center center;
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  & .container-game{
   width: 100%;
    height: 100%;
    padding: 18px 16px 18px;
  }
  & .game_box{
    flex-direction: column;

  }
  & .game_container{
    position: relative;
    width: 100%;

    & .countdown_box{
      left: 20px;
    }
  }
}
.start_box{
  align-items: center;
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.start_game{
  background: none;
  border: none;
  background: #FFFFFF;

  border-radius: 4px;
  font-weight: 500;
  font-size: 20px;
  color: #333333;
  text-align: center;
  width: 280px;
  height: 64px;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  &:hover{
    box-shadow: 0 2px 4px 0 #333333;
  }
}
.container_btn{
  padding: 20px 0;
}
.regulations{
  position: relative;
  max-width: 800px;
  overflow: auto;
  text-align: center;
  height: 100%;
  display: flex;
  align-items: center;
  & .regulations_container{
    position: relative;
    max-width: 800px;
    max-height: 100%;
    overflow: auto;
    & .about_game{
      font-weight: 400;
      font-size: 20px;
      color: #FFFFFF;
      letter-spacing: 0;
      line-height: 40px;
    }
  }
}

@media all and (max-width: 535px){
  .mainMemory{
    height: 100%;
  }
}