.games__wrapper{
  margin-top: 60px;
  position: relative;
}
.game-preview{
  background: #FFFFFF;
  border: 1px solid #FAF7F7;
  box-sizing: border-box;
  box-shadow: 0px -2px 3px rgba(0, 0, 0, 0.06), 0px 18px 16px rgba(0, 0, 0, 0.05);
  border-radius: 30px;
  padding: 45px 25px 44px;
  width: 220px;
  text-align: center;
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;
  transition: 0.5s all ease;
  &:hover{
    bottom: 10px;
  }
  span{
    font-size: 24px;
    font-weight: bold;
  }
  .svg-inline--fa{
    margin: 0 auto 20px;
    width: 70px;
    height: 70px;
  }
  svg path{
    fill: #063740;
  }
}