.words-field {
  display: flex;
  width: 100%;
  max-width: 768px;
  justify-content: space-around;
  .words-field__word {
    cursor: pointer;
    color: #ffffff;
    text-align: center;
    font-size: 1.2em;
    transition: .3s;
    text-transform: uppercase;
    &:hover {
      background-color: #d7d7d733;
    }
  }
}
@media (max-width: 992px) {
  .words-field .words-field__word {
    font-size: 0.8rem;
  }
}